}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Heading:hover{
  color: aqua;
} 

.largetext{
  font-size: large;
}

.iconAlignCenter{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.resumeHeader::after{
  content: "";
  width: 150px;
  height: 2px;
  display: inline-block;
  background: white;
  margin: 6px 5px;
  text-align: left;
}
.nav-link{
  font-size: larger;
  font-weight:500;
  color: white !important;
}
.nav-link:hover,.active {
  color:aquamarine !important; /*will change color of text within the element on hover */
} 
.cardbody{
  background-color: rgba(9, 30, 73,0.9);
}
.card{
  background:transparent !important ; 
  border-radius: 10px;
}
.card:hover .cardbody{
  background-color: rgba(0,22,22,.9);
  transition: ease-in-out;
  transition: cubic-bezier(0.075, 0.82, 0.165, 1);
  transition-duration: 2000ms;
}

.nav-link:before {
  content: "";
  position: absolute;
  width: 0;
  height: 2px;
  bottom: 15px;
  background-color:yellow;
  visibility: hidden;
  width: 0px;
  transition: all 0.3s ease-in-out 0s;
}
.active:before{
  visibility: visible;
  width: 25px;
} 

.nav-link:hover:before, .nav-link:hover > a:before, .active > a:before {
  visibility: visible;
  width: 25px;
  color:aquamarine !important;
}

.nav-link:hover, .nav-link .active > a, .nav-link :hover > a {
  color: aquamarine;
  text-decoration: none;
}
.heading{
  
  text-align: justify;
  /* display: table-row; */
  padding-left: 5%;
  position: relative;
}

.HomeMain{
  position:absolute;
  top:40%;
  width: 100%;
}

@media only screen and (max-width: 575px) {
  .NavbarforMobile{
    display: block;
  }
  .NavbarforLargeDiplay{
